import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { Redirect } from 'react-router-dom';
import { actionCreators } from './store';
import { 
    ChatboxWrapper, 
    ChatboxTitle, 
    ChatboxList, 
    ChatboxItem, 
    ChatboxBtm, 
    ChatboxInput, 
    Button 
} from './style';

class Chatbot extends PureComponent {
    render() {
        const { loginStatus } = this.props
        if ( !loginStatus ) {
            const { inputValue, changeInputValue, handleClick, list } = this.props;
            return (
                <ChatboxWrapper>
                    <ChatboxTitle>Tutor assistant</ChatboxTitle>
                    <ChatboxList>
                        {
                            list.map((item,index) => {
                                return (                          
                                    <ChatboxItem key={index}>
                                        {item}
                                    </ChatboxItem>                                        
                                );
                            })
                        }
                    </ChatboxList>
                    <ChatboxBtm>
                        <ChatboxInput value={inputValue} onChange={changeInputValue} /> 
                        <Button className='enter' onClick={handleClick}>Enter</Button>
                    </ChatboxBtm>
                </ChatboxWrapper>
            )
        } else {
            return <Redirect to='/login'/>
        }
    }
}

const mapState = (state) => ({
    loginStatus: state.getIn(['login', 'login']),
    inputValue: state.getIn(['chatbot', 'inputValue']),
    list: state.getIn(['chatbot', 'messageList'])
});

const mapDispatch = (dispatch) => {
     return {
        changeInputValue(e) {
            dispatch(actionCreators.changeInputValue(e));
        },

        handleClick() {
            dispatch(actionCreators.handleClick());
        }
    }
}

export default connect(mapState, mapDispatch)(Chatbot);