import styled from 'styled-components';

export const ChatboxWrapper = styled.div`
    border: 5px solid #ea6f5a;
    border-radius: 10px;
    width: 800px;
    height: 630px;
    margin-top: 10px;
    margin-left: 350px;
    margin-bottom: 10px;
`;

export const ChatboxTitle = styled.div`
    width: 800px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    background: #eee;
`;

export const ChatboxList = styled.div`
    margin: 20px 0;
    height: 495px;
`;

export const ChatboxItem = styled.div`
    margin: 18px 0;
    display: inline-block;
    position: relative;
    background: #25a23b;    
    border-radius: 5px;
    color: white;
    padding: 5px;
    line-height: 25px;   
    &.input {
        margin-left: 45%;
        width: 50%;
    };
    &.output {
        margin-left: 3%;
        width: 70%;
    }; 
`;

export const ChatboxBtm = styled.div`
    background: #999;
`;

export const ChatboxInput = styled.input.attrs({
    placeholder: 'Please type in your question'
})`
    width: 670px;
    height: 38px;
    padding: 0 30px 0 20px;
    margin-top: 9px;
    margin-left: 10px;
    margin-bottom: 9px;
    box-sizing: border-box;
    border: none;
    outline: none;
    border-radius: 5px;
    background: #eee;
    font-size: 14px;
    text-align: center;
    color: #666;
    &::placeholder {
        color: #999;
    }
`;

export const Button = styled.div`
    float: right;
    margin-top: 9px;
    margin-right: 10px;
    width: 100px;
    line-height: 38px;
    text-align: center;
    border-radius: 10px;
    border: 1px solid #ec6149;
    font-size: 14px;
    background: white;
    &.enter{
        color: #ec6149;
    }
`;